
@use "@styles/variables" as *
@use "@styles/mixins" as *

.project-item__link
	display: flex
	flex-direction: column
	gap: 8px
	width: 100%
	font-weight: 500
	font-size: 18px
	line-height: 27px
	color: $black-75
	transition: all .2s ease-in-out

	&:hover
		color: $blue

		.project-item__image
			transform: scale(1.01)

.project-item__image-wrapper
	position: relative
	width: 100%
	padding-top: 100%
	transition: all .2s ease-in-out

.project-item__image
	position: absolute
	top: 0
	left: 0
	width: 100%
	height: 100%
	object-fit: cover
	object-position: center center
	transition: all .2s ease-in-out
