
@use "@styles/variables" as *
@use "@styles/mixins" as *

.footer
	margin: 0
	padding: 0
	background: $dark-blue-gradient

.footer__container
	display: flex
	justify-content: space-between
	align-items: center
	height: inherit
	margin: auto
	padding: 60px 0

	@include screen(xs)
		flex-direction: column-reverse
		gap: 16px
		padding: 18px 0

.footer__social-list
	display: flex
	justify-content: space-between
	align-items: center
	gap: 30px
	list-style: none
	margin: 0
	padding: 0

.footer__social-link
	display: block
	width: 35px
	height: 35px
	background-color: $white-light
	mask-repeat: no-repeat
	mask-position: center
	mask-size: contain

	&:hover
	opacity: 0.8

	&:active
	opacity: 0.7

	@include screen(sm)
		width: 25px
		height: 25px
		mask-size: 25px 25px
		padding: 5px

.footer__tel-link
	font-weight: 100
	font-size: 28px
	line-height: 33px
	color: $white

	@include screen(sm)
		font-size: 18px
		line-height: normal
