
@use "@styles/variables" as *
@use "@styles/mixins" as *

.header
	position: fixed
	top: 0
	width: 100%
	height: auto
	background-color: rgba(0, 0, 0, 0.75)
	backdrop-filter: blur(50px)
	z-index: 1
	margin: 0

	@include screen(sm)
		height: 40px

		&--opened
			backdrop-filter: unset

.header__container
	display: flex
	justify-content: space-between
	align-items: center
	gap: 60px

.header__logo
	display: block
	width: 240px
	height: auto

	@include screen(sm)
		width: 185px

.header__navigation-list
	display: flex
	justify-content: flex-end
	gap: min(20px, 1.3vw)
	list-style: none
	margin: 0
	padding: 0

	@include screen(sm)
		position: absolute
		top: 0
		left: 0
		right: 0
		flex-direction: column
		justify-content: flex-start
		align-items: flex-end
		height: 100vh
		background-color: $black-75
		padding: 40px min(5vw, 24px)
		backdrop-filter: blur(50px)
		z-index: -1

.header__navigation-item
	margin: 0
	padding: 0

.header__navigation-link
	position: relative
	display: block
	font-weight: 100
	font-size: 18px
	color: $white
	margin: 0
	padding: 15px
	border-radius: 2px
	transition: all .2s ease-in-out

	&:visited
		color: $white

	&:hover
		color: $white
		background-color: rgba(128, 128, 128, 0.7)

	@include screen(sm)
		&:hover
			background-color: transparent

.router-link-active::after,
.router-link-exact-active::after
	content: ''
	position: absolute
	bottom: 0
	left: 15px
	right: 15px
	height: 3px
	background-color: $white
	border-radius: 3px 3px 0 0
	opacity: 0.7

	@include screen(sm)
		content: none
