
@use "@styles/variables" as *
@use "@styles/mixins" as *

.about-section
	background-color: $blue-dark
	background-image: url('@images/bg.jpg')
	// background-image: -webkit-image-set(url('@images/bg.webp') type('image/webp'), url('@images/bg.jpg') type('image/jpeg'))
	background-image: image-set(url('@images/bg.webp') type('image/webp'), url('@images/bg.jpg') type('image/jpeg'))
	background-repeat: no-repeat
	background-size: 100% 100%
	background-position: center
	background-size: cover
	padding: 120px 0 70px

	@include screen(sm)
		background-image: url('@images/bg-mobile.jpg')
		// background-image: -webkit-image-set(url('@images/bg-mobile.webp') type('image/webp'), url('@images/bg-mobile.jpg') type('image/jpeg'))
		background-image: image-set(url('@images/bg-mobile.webp') type('image/webp'), url('@images/bg-mobile.jpg') type('image/jpeg'))

.about-section__grid
	display: grid
	grid-template-columns:  284px 1fr
	gap: 56px
	width: 100%
	margin: 0 auto

	@include screen(sm)
		display: block

.about-section__portrait
	img
		width: 100%
		height: auto

.about-section__story
	color: $white
	font-family: $font-sf
	font-style: normal

.about-section__story-heading
	font-weight: 600
	font-size: 48px
	line-height: 57px
	text-align: left
	margin: 0 0 24px

.about-section__story-text
	font-weight: 400
	font-size: 18px
	line-height: 27px
	letter-spacing: -0.03em
	text-align: left
	margin: 0 0 16px

	a
		color: $orange

		&:visited
			color: $orange

.recent-section
	padding: 50px 0

.recent-section__list
	display: grid
	grid-template-columns:  1fr 1fr 1fr
	grid-column-gap: 20px
	grid-row-gap: 36px
	list-style: none
	margin: 0px
	padding: 0px

	@include screen(sm)
		grid-template-columns:  1fr 1fr

	@include screen(xs)
		grid-template-columns:  1fr
